@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Inter&family=Noto+Sans+KR:wght@300;400;500;700&family=Titillium+Web:wght@400;600;700;900&family=Inter:wght@300;400;500&display=swap');
:root {
    --main-color: #ffea72;
}

html,body{width:100%;height:100%; background: #f9f9f9;}
*{box-sizing: border-box; margin: 0; padding: 0;}

canvas {
    width: 100%;
    height: 100%;
}

/* 여백 초기화 */
body,div,ul,li,dl,dd,dt,ol,h1,h2,h3,h4,h5,h6,input,fieldset,legend,p,select,table,th,td,tr,textarea,button,form,figure,figcaption{margin:0; padding:0;}

/* a 링크 초기화 */
a {text-decoration: none; }
a:hover{color: #222;}

/* 폰트 초기화 */
body, input, textarea, select, button, table {font-family:'Inter', 'Nanum Gothic','Titillium Web',sans-serif; color: #222; font-size: 14px; line-height: 1.5;}

/* 폰트 스타일 초기화 */
em,address {font-style: normal;}

/* 블릿기호 초기화 */
ul,li,ol {list-style:none;}

/* 제목 태그 초기화 */
h1,h2,h3,h4,h5,h6 {font-size: 13px; font-weight: normal;}

/* 테두리 초기화 */
img,fieldset {border:0 none;}

/* 버튼 초기화 */
button {outline: none; border: none;}

/* 반응형 */
/* video,img {width: 100%;} */

/* clearfix */
.cf{*zoom:1;}
.cf:after {display: block; content: ''; clear: both;}


/* IR 효과 */
.ir_pm {display:block; overflow:hidden; font-size:0; line-height:0; text-indent:-9999px;} /* 의미있는 이미지의 대체 텍스트를 제공하는 경우(Phark Method) */
.ir_wa {display:block; overflow:hidden; position:relative; z-index:-1; width:100%; height: 100%;} /* 의미있는 이미지의 대체 텍스트로 이미지가 없어도 대체 텍스트를 보여주고자 할 때(WA IR) */
.ir_so {overflow: hidden; position:absolute; width:0; height:0; line-height:0; text-indent:-9999px;} /* 대체 텍스트가 아닌 접근성을 위한 숨김 텍스트를 제공할 때 */



/* input style */
input {background-image: none;}
input[type=button],
input[type=reset],
input[type=text],
input[type=password],
input[type=submit],
input[type=search],
input[type=tel],
input[type=email] {
    -webkit-appearance: none;
    border-radius: 0
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
    -webkit-appearance: none
}
input:checked[type=checkbox] {
    background-color: #666;
    -webkit-appearance: checkbox
}







